import { useEffect } from 'react';
import gsap from "gsap";
import { TextPlugin } from "gsap/TextPlugin";
import { CSSRulePlugin } from "gsap/CSSRulePlugin";
import "../Styles/About.css";

gsap.registerPlugin(TextPlugin);
gsap.registerPlugin(CSSRulePlugin);

function About() {

    useEffect(() => {
        const hl = CSSRulePlugin.getRule(".hl:after");

        let tl = gsap.timeline({ repeat: -1, delay: .5 });
        tl.to('.t1', 0, { text: "Hi, I'm" })
            .to('.t2', 0, { text: "Mohamed Elhaj", })
            .to('.t3', 0, { text: "A motivated<span class='hl h1'>&nbsp;problem solver&nbsp;</span> who enjoys coding and building solution" })
            // drop down
            .to('.t1', 1, { y: 0 })
            .to('.t2', 1, { y: 0 }, "-=.7")
            .to('.t3', 1, { y: 0, delay: .2 })

            .to(hl, .5, { transform: "scale(1)", delay: .5 })
            .to(hl, .5, { transform: "scale(0)", delay: 7 })

            // hide
            .to('.t1', 1, { y: "-150" })
            .to('.t2', 1, { y: "-150" }, "-=.7")
            .to('.t3', 1, { y: "150" }, "-=.4")


            // Change text
            .to('.t1', 0, { text: "I'm a", y: "-150px" })
            .to('.t2', 0, { text: "Front-end Developer", y: "-150px" })
            .to('.t3', 0, { text: "I Enjoy building front-end using <span class='hl h1'>&nbsp;React&nbsp;</span>, Angular, React Native and more", y: "150px" })

            .to('.t1', 1, { y: 0 })
            .to('.t2', 1, { y: 0 }, "-=.7")
            .to('.t3', 1, { y: 0, delay: .2 })

            .to(hl, .5, { transform: "scale(1)", delay: .5 })
            .to(hl, .5, { transform: "scale(0)", delay: 7 })

            // hide
            .to('.t1', 1, { y: "-150" })
            .to('.t2', 1, { y: "-150" }, "-=.7")
            .to('.t3', 1, { y: "150" }, "-=.4")


            // Change text
            .to('.t1', 0, { text: "also a", y: "-150px" })
            .to('.t2', 0, { text: "Backend Developer", y: "-150px" })
            .to('.t3', 0, { text: "Experienced in <span class='hl h1'>&nbsp;Node.js&nbsp;</span>, Express, Django, Laravel, mongoose, and more", y: "150px" })

            .to('.t1', 1, { y: 0 })
            .to('.t2', 1, { y: 0 }, "-=.7")
            .to('.t3', 1, { y: 0, delay: .2 })

            .to(hl, .5, { transform: "scale(1)", delay: .5 })
            .to(hl, .5, { transform: "scale(0)", delay: 7 })

            // hide
            .to('.t1', 1, { y: "-150" })
            .to('.t2', 1, { y: "-150" }, "-=.7")
            .to('.t3', 1, { y: "150" }, "-=.4")

            
    }, [])

    return (
        <section id="about" className="about flex flex-column center">
            <div className="container">
                <div className="flex flex-column center text-center p20 about-text">

                    <div className="overflow-hidden position-relative">
                        <h1 className="t1">Hi, I'm</h1>
                    </div>

                    <div className="overflow-hidden position-relative">
                        <h1 className="t2">Mohamed Elhaj</h1>
                    </div>

                    <div className="overflow-hidden position-relative">
                        <p className="t3">A motivated<span className="hl h1">&nbsp;problem solver&nbsp;</span> who enjoys coding and building solution</p>
                    </div>

                </div>
            </div>
        </section>
    );
}

export default About;
